import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Section from './Section'
// import store from '../store'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  name: {
    displayName: 'tableName',
  },
  sectionId: {
    displayName: 'sectionName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Section.getList,
        itemText: 'name',
      },
    },
  },
  seatCount: {
    formConfig: {
      default: 4,
    },
  },
  usageStatus: {
    type: IKDataEntity.Types.Boolean,
    form: false,
  },
  usageCount: {
    form: false,
  },
  // sqCode: {
  //   type: IKDataEntity.Types.Image,
  //   formConfig: {
  //     type: {
  //       root: () => GlobalSettings.SQR + store.getters.rootUrl + GlobalSettings.genSQUrl + 'TableName=',
  //     },
  //     required: false,
  //   },
  //   displayName: 'image',
  //   form: false,
  // },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Tables.php?op=add', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Tables.php?op=update', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Tables.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Tables.php', {
      op: 'currentInfos',
      lang: GlobalSettings.getLang(),
      ...filter,
    })).content.map(function (item) {
      item.sqCode = item.name
        return item
      })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
