<template>
  <ik-data-table
    :model="model"
    icon="mdi-table-chair"
    :entity-name=" $t('桌子')"
  >
    <template v-slot:filterRight>
      <v-btn
        small
        color="green darken-2"
        dark
        @click="printQR"
      >
        {{ $t('打印二维码') }}
        <v-icon
          dark
          right
        >
          mdi-plus
        </v-icon>
      </v-btn>
      <v-btn
        small
        class="ml-2 mr-2"
        color="blue darken-2"
        dark
        @click="addAdvancedMultipleTable"
      >
        {{ $t('批量添加桌子') }}
        <v-icon
          dark
          right
        >
          mdi-plus
        </v-icon>
      </v-btn>
      <v-dialog
        v-model="dialogAdvanced"
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            <span class="headline">{{ $t('批量添加桌子') }}</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  cols="12"
                  md="6"
                  sm="6"
                >
                  <v-select
                    v-model="advancedItem.sectionId"
                    :items="sectionList"
                    item-text="name"
                    item-value="id"
                    :label="$t('sectionName')"
                    required
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                  sm="12"
                >
                  <v-text-field
                    v-model="advancedItem.start"
                    :label="$t('start')"
                    required
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                  sm="12"
                >
                  <v-text-field
                    v-model="advancedItem.end"
                    :label="$t('end')"
                    required
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                  sm="12"
                >
                  <v-switch
                    v-model="advancedItem.digit"
                    :label="$t('digit')"
                  />
                </v-col>
                <v-col
                  cols="12"
                  md="6"
                  sm="12"
                >
                  <v-text-field
                    v-model="advancedItem.seatCount"
                    :label="$t('seatCount')"
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn
              color="blue darken-1"
              text
              @click="closeAdvancedDialog"
            >
              {{ $t('Cancel') }}
            </v-btn>
            <v-btn
              color="blue darken-1"
              @click="saveAdvancedDialog"
            >
              {{ $t('Save') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </template>
  </ik-data-table>
</template>

<script>

  import { IkDataTable } from 'metaflow-js'
  import Table from '../../model/Table'
  import Section from '../../model/Section'
  import { SetMultipleTable, SetAdvancedMultipleTable, printAllQR } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: Table,
        sectionModel: Section,
        sectionList: [],
        dialog: false,
        dialogAdvanced: false,
        editedItem: {
          sectionId: '',
          tableAmount: '',
          seatCount: '4',
        },
        advancedItem: {
          sectionId: '',
          start: '',
          end: '',
          digit: 0,
          seatCount: '4',
        },
      })
    },
    created () {
      this.getSectionList()
    },
    methods: {
      addMultipleTable () {
        this.dialog = true
      },
      addAdvancedMultipleTable () {
        this.dialogAdvanced = true
      },
      printQR () {
        printAllQR().then(() => {
          Utils.toast('Successful!')
        })
      },
      saveAdvancedDialog () {
        SetAdvancedMultipleTable(this.advancedItem.sectionId, this.advancedItem.start, this.advancedItem.end, this.advancedItem.digit, this.advancedItem.seatCount).then((res) => {
          Utils.toast('Successful!')
          this.closeAdvancedDialog()
        })
      },
      closeAdvancedDialog () {
        this.dialogAdvanced = false
      },
      save () {
        SetMultipleTable(this.editedItem.sectionId, this.editedItem.tableAmount, this.editedItem.seatCount).then((res) => {
          Utils.toast('Successful!')
          this.close()
        })
      },
      close () {
        this.dialog = false
      },
      async getSectionList () {
        this.sectionList = await Section.getList()
      },
      goNext (item) {
        this.$router.push({
          name: 'Tabellendetailseite',
          params: { id: item.id },
        })
      },
    },
  }
</script>
